import React from "react";
import { Link } from "react-router-dom";
import {
  Container,
  Col,
  Row,
  Button,
  Card,
  Alert,
  Form,
} from "react-bootstrap";
import { useForm } from "react-hook-form";
import registerResolver from "../../validations/registerResolver";

const Register = () => {
  const {
    register,
    handleSubmit,
    formState: { errors, dirtyFields },
    reset,
  } = useForm({ resolver: registerResolver });

  const onSubmit = (formData) => {
    console.log(formData);
    //post request
    reset();
  };

  return (
    <>
      <div
        style={{
          backgroundImage: "linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%)",
          backgroundRepeat: "no-repeat",
          objectFit: "fill",
        }}
      >
        <Container style={{ minHeight: "100vh", paddingTop: "80px" }}>
          <Row>
            <Col className="mt-4">
              <Card
                style={{ maxWidth: "600px", height: "auto" }}
                className="p-4 mx-auto my-4"
              >
                <h1>Register</h1>
                <Form onSubmit={handleSubmit(onSubmit)}>
                  <Form.Group>
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                      type="text"
                      {...register("name")}
                      placeholder="Name"
                      className="mb-2"
                    ></Form.Control>
                    {errors?.name && (
                      <Form.Text>
                        <Alert variant="danger">{errors.name.message}</Alert>
                      </Form.Text>
                    )}
                  </Form.Group>
                  <Form.Group>
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="email"
                      {...register("email")}
                      placeholder="Email"
                      className="mb-2"
                    ></Form.Control>
                    {errors?.email && (
                      <Form.Text>
                        <Alert variant="danger">{errors.email.message}</Alert>
                      </Form.Text>
                    )}
                  </Form.Group>
                  <Form.Group>
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      type="password"
                      {...register("password")}
                      placeholder="Password"
                      className="mb-2"
                    ></Form.Control>
                    {errors?.password && (
                      <Form.Text>
                        <Alert variant="danger">
                          {errors.password.message}
                        </Alert>
                      </Form.Text>
                    )}
                  </Form.Group>
                </Form>
                <button
                  className="btn btn-info mt-2"
                  onClick={handleSubmit(onSubmit)}
                >
                  Register
                </button>
                <p className="text-center" style={{ marginTop: "60px" }}>
                  Already have an account? <Link to="/login">Login</Link>
                </p>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
};

export default Register;
